import React, {useState} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import { Alert, AlertTitle } from '@material-ui/lab';
import style from './Form.module.css';

const useStyles = makeStyles({
    root: {
        width: 60+'%',
        opacity: 90+'%',
    },
    cardContent: {
        display: 'flex',
        justifyContent: 'space-around',
        flexWrap: 'wrap',
    },
    input: {
        width: 100+'%',
        margin: 20+'px'
    },
    btn: {
        margin: 35+'px',
        marginTop: 0
    }
});

export default function Form(props) {
    const classes = useStyles();
    const [field, setField] = useState({userName: "", password: "", repeatPassword: ""});
    const [error, setError] = useState("");

    function getUserName(event){
        setField(prevField => ({...prevField, userName: event.target.value}));
    };

    function getPassword(event){
        setField(prevField => ({...prevField, password: event.target.value}));
    };

    function getRepeatPassword(event) {
        setField(prevField => ({...prevField, repeatPassword: event.target.value}));
    }

    async function validate() {
        console.log(error);
        if (!field.userName.trim()) return setError("The UserName field is empty.");
        console.log(1);
        if (!field.password.trim()) return setError("The Password field is empty.");
        console.log(2);
        if (!field.repeatPassword.trim()) return setError("The Password Repetition field is empty.");
        console.log(3);
        if (field.password !== field.repeatPassword) return setError("The Password and Password Repetition field are not equal.");
        console.log(4);
        await props.clickHandler(field.userName, field.password);
        setError(props.error)

    }

    // React.useEffect(() => {
    //     if((props.error) && (props.error !== 'success')) setError(props.error);
    // });

    return (
        <div className={style.w100}>
            {console.log("Test")}{(error)?[<Alert severity="error" className={style.alert}>
                <AlertTitle style={{width: 'max-content'}}>Error</AlertTitle>
                <strong>{error}</strong>
            </Alert>]:<div className={style.alert} style={{height: 63.6167+"px"}}></div>}
            <Card className={classes.root}>
                <CardContent className={classes.cardContent}>
                    <TextField onChange={getUserName} className={classes.input} id="user-name" label="User Name" variant="outlined" />
                    <TextField onChange={getPassword} type="password" className={classes.input} id="password" label="Password" variant="outlined" />
                    <TextField onChange={getRepeatPassword} type="password" className={classes.input} id="repeat-password" label="Password Repetition" variant="outlined" />
                </CardContent>
                <CardActions>
                    <Button className={classes.btn} onClick={validate} variant="contained" color="primary">
                        SIGNUP
                    </Button>
                </CardActions>
            </Card>
        </div>
    );
}
