import React from 'react';
import './Signup.css';
import Form from "./Form";
import Axios from "axios";
import ParticlesBg from "particles-bg";
import Card from "@material-ui/core/Card";
import StepsComponent from "./StepsComponet";




class Signup extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            error: ""
        };
    }

    sendData = (username, password) => {
        Axios.post(/*'https://api.mocki.io/v1/6910a074*/'/auth/signup', {username: username, password: password}
        ).then(res => {
            if (res.data.msg.trim() === 'success') {
                this.props.success();
                this.setState({error: ""});
            } else {
                this.setState({error: res.data.msg});
            }
        });
    }

    render() {
        return(
            <div className='Signup'>
                <ParticlesBg type="color" bg={true}/>
                <Card>
                    <StepsComponent />
                    <Form clickHandler={this.sendData} error={this.state.error} />
                </Card>
            </div>
        );
    }
}



export default Signup;
